(function(){
	'use strict'

	angular
    	.module('notify')
    	.factory('notifyService', notifyService);

    notifyService.$inject = ['$timeout']

    function notifyService($timeout) {

    	var service = {
    		
    		info: info,
            messages : []
    	};

        

    	return service;

    	////////////

    	function error() {
	      /* */
	    }

	    function info(message) {
          console.log("info called");
          service.messages.push(message);

          $timeout(function(){
            service.messages.splice(0,1);
          },2000)

	    }


	    function success() {
	      /* */
	    }


    }

	
// end IIFE
})();

